.timeline-wrapper {
  min-height: 100vh;
  color: #fff;
  position: relative;
  
}

.section {
  height: 100vh;
  font-size: 25px;
  font-weight: 100;
  background-color: #22a7f0;

  display: flex;
  justify-content: center;
  align-items: center;

  $colors: white, black, rgb(10, 35, 104), rgb(23, 24, 124), rgb(8, 9, 59),
    rgb(1, 2, 70), rgb(2, 14, 27), black, rgb(25, 25, 43), rgb(23, 24, 124),
    rgb(1, 33, 70), rgb(10, 35, 104), rgb(2, 14, 27), black;

  @for $i from 1 through length($colors) {
    &:nth-child(#{$i}) {
      background: nth($colors, $i);
    }
  }
}

.info-wrapper {
  width: 50%;
}

.tnav {
  &__wrapper {
    position: fixed;
    height: 100%;
    padding: 0px;

    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
  }

  @media screen and (min-width: 2160px) {
    & {
      margin: 0 0 20px 29px;
    }

    &__date {
      font: normal normal normal 20px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (min-width: 1311px) and (max-width: 2159px) {
    & {
      margin: 0 0 20px 29px;
    }

    &__date {
      font: normal normal normal 20px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1310px) {
    & {
      margin: 0 0 20px 29px;
    }

    &__date {
      font: normal normal normal 19px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (min-width: 580px) and (max-width: 767px) {
    & {
      margin: 0 0 20px 15px;
    }

    &__date {
      font: normal normal normal 14px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (min-width: 415px) and (max-width: 579px) {
    & {
      margin: 0 0 20px 13px;
    }

    &__date {
      font: normal normal normal 12px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }

  @media screen and (max-width: 414px) {
    & {
      margin: 0 0 20px 11px;
    }

    &__date {
      font: normal normal normal 12px/1.4em Roboto Regular;
      transition: all 0.3s ease-out;
    }

    &__year {
      letter-spacing: 0.05rem;
    }
  }
}

