  @media screen and (min-height: 401px) and (max-height: 500px) {
    margin-bottom: 0.4em;
  }

  @media screen and (max-height: 400px) {
    margin-bottom: 0em;
  }

  @media screen and (min-width: 2160px) {
    &:after {
      content: "";
      display: block;
      border-left: 3px solid white;
      border-top: 3px solid white;
      height: 250px;
      width: 20px;
      position: absolute;
      left: -30px;
      top: 14px;
    }

    .tnav-a {
      display: block;
      padding: 0;
      color: #fff;
      transition: all 0.15s ease-out;
      text-decoration: none;

      &:hover {
        background-color: transparent;
        padding-left: 0.4em;
      }

      &:focus {
        background-color: transparent;
      }
    }

    &.active {
      pointer-events: none;
      padding-left: 0.3em;

      &:after {
        width: 28px;
        height: 400px;
        top: 20px;
        transition: all 0.3s ease-in-out;
      }

      .tnav__date {
        font: normal normal normal 29px/1.4em Roboto-Medium;
      }
    }
  }

